import React from "react"
import { Link } from "gatsby"

class Header extends React.Component {
  state = {
    menuExpanded: false,
  }
  toggleMenuExpansion() {
    this.setState({
      menuExpanded: !this.state.menuExpanded
    })
  }
  render() {
    const menuClassName = this.state.menuExpanded ? `expanded` : ``
    return (
      <header>
        <section>
          <Link to="/" title="Home">Kuokoa Studio</Link>
          <a title="Expand Navigation Menu" onClick={() => {this.toggleMenuExpansion()}}>Expand</a>
        </section>
        <menu className={menuClassName}>
          <Link to="/ethos">Ethos</Link>
          <Link to="/collaborate">Collaborate</Link>
          <Link to="/works">Works</Link>
          <Link to="/journal">Journal</Link>
          <a href="https://kuokoa.life">Trade</a>
          <a href="mailto:aloha@kuokoa.studio">Aloha</a>
        </menu>
      </header>
    )
  }
}

export default Header
